@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color:  rgb(250, 225, 91);
  /* background-image: linear-gradient(45deg, rgb(56, 56, 56), rgb(247, 213, 91)); */
}

.container {
  max-width: 600px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  padding: 30px;
  border-radius: 20px 20px 0px 0px;
  background-color: #fff;
  height: 100%;
  position: relative;
  bottom: 0px; 
  box-shadow: -20px 2px;
}

